body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  transition: background-color .3s, color .3s;
}

body.light-theme {
  color: #333;
  background-color: #f5f5f5;
}

body.light-theme .diary-entry-form {
  background-color: #fff;
  box-shadow: 0 2px 5px #0000001a;
}

body.light-theme .full-width-input {
  color: #333;
  background-color: #fff;
  border: 1px solid #ddd;
}

body.light-theme .full-width-button {
  color: #fff;
  background-color: #007bff;
}

body.light-theme .full-width-button:disabled {
  background-color: #ccc;
}

body.dark-theme {
  color: #f5f5f5;
  background-color: #222;
}

body.dark-theme .diary-entry-form {
  background-color: #333;
  box-shadow: 0 2px 5px #0000004d;
}

body.dark-theme .full-width-input {
  color: #f5f5f5;
  background-color: #333;
  border: 1px solid #555;
}

body.dark-theme .full-width-button {
  color: #fff;
  background-color: #0056b3;
}

body.dark-theme .full-width-button:disabled {
  background-color: #555;
}

.App {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.App h1 {
  color: #4a6baf;
  border-bottom: 1px solid #8fa4d0;
  padding-bottom: .5rem;
}

.responsive-form {
  width: 100%;
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  margin-bottom: 8px;
  font-weight: bold;
  display: block;
}

.full-width-input {
  box-sizing: border-box;
  border-radius: 4px;
  width: 100%;
  padding: 12px;
  font-size: 16px;
}

.full-width-button {
  cursor: pointer;
  border: none;
  border-radius: 4px;
  width: 100%;
  padding: 12px;
  font-size: 16px;
  transition: background-color .3s;
}

.full-width-button:disabled {
  cursor: not-allowed;
}

.App-main {
  overflow-x: hidden;
}

.profile-container {
  margin-bottom: 20px;
}

.diary-entry-form {
  border-radius: 8px;
  margin-bottom: 30px;
  padding: 20px;
}

@media (width <= 768px) {
  .App {
    padding: 0 10px;
  }

  .App-header {
    padding: 10px 0;
  }

  .diary-entry-form {
    padding: 10px;
  }

  .full-width-input, .full-width-button {
    font-size: 14px;
  }
}
